.main {
  height: 85vh;
  background-color: #f2f2f2;
  overflow: auto;
}

.wrapper {
  height: 43%;
  width: 50%;
  background-color: #fff;
  margin: 12rem auto 3rem auto;
  border-radius: 10px;
  box-shadow: 0px 5px 15px 5px #d0d0d0;
}

.header {
  padding: 1rem 0 .5rem 0;
  font-weight: 400;
}

.inputField {
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
  display: inline-block;
  width: 75%;
  margin-bottom: .5rem;
}

.errorMessage {
  color: red;
}

.button {
  background-color: #4CAF50;
  border: none;
  color: white;
  padding: 8px 22px;
  text-align: center;
  -webkit-text-decoration: none;
          text-decoration: none;
  display: inline-block;
  font-size: 16px;
  margin: .75rem 0 1.75rem 0;
  cursor: pointer;
  border-radius: 30px;
}

.dialogoverlay {
  display: none;
  opacity: .8;
  position: fixed;
  top: 0px;
  left: 0px;
  background: #FFF;
  width: 100%;
  z-index: 10;
  padding: 20px;
}

.dialogbox{
  display: none;
  z-index: 10;
  position: fixed;
  background: #e5e5e5;
  border-radius: 7px;
  width: 550px;
  padding: 20px;
}

.dialoghead {
  background:  #e5e5e5;
  font-size:19px;
  padding: 10px;
  color: black;
}

.dialogbody {
  background: #e5e5e5;
  padding: 20px;
  color: #3cB650;
}

.dialogfoot {
  background: #e5e5e5; 
  padding: 10px;
  text-align: right;
}

.alertbutton {
  background-color: #4CAF50;
  border: none;
  color: white;
  padding: 0 22px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  margin: 4px 2px;
  cursor: pointer;
  border-radius:30px;
  height: 2rem;
}